import styled from "styled-components"
import { mobile } from "../responsive"
import {
	Facebook,
	Instagram,
	LinkedIn,
	Twitter,
	Room,
	Phone,
	MailOutline,
  } from "@material-ui/icons";


const Container = styled.footer`
  display: flex;
  background-color: #f5fbfd;
  ${mobile({ flexDirection: "column" })};
`
const Left = styled.div`
	flex: 1;
	display: flex;
	flex-direction: column;
	padding: 20px;
`
const Logo = styled.h1`
`
const Description = styled.p`
  	margin: 20px 0px;
	`

const SocialContainer = styled.div`
	display: flex;
`
const SocialIcon = styled.div`
  width: 40px;
  height: 40px;
  border-radius: 50%;
  color: white;
  background-color: #${props => props.color};
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 20px;
`

const Center = styled.div`
	flex: 1;
	padding: 20px;
	${mobile({ display: "none" })}
`

const Title = styled.h3`
  margin-bottom: 30px;
`
const List = styled.ul`
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
`
const ListItem = styled.li`
  width: 50%;
  margin-bottom: 10px;
`

const Right = styled.div`
	flex: 1;
	padding: 20px;
`

const ContactItem = styled.div`
  margin-bottom: 20px;
  display: flex;
  align-items: center;
`

const Payment = styled.img`
  width: 50%;
`

const Footer = () => {
  return (
	<Container>
		<Left>
			<Logo>WeedsBGone</Logo>
			<Description>
				Trish
			</Description>
			<SocialContainer>
				<SocialIcon color='3b5999'>
					<Facebook />
				</SocialIcon>
				<SocialIcon color='55acee'>
					<Twitter />
				</SocialIcon>
				<SocialIcon color='e4405f'>
					<Instagram />
				</SocialIcon>
				<SocialIcon color='e60023'>
					<LinkedIn />
				</SocialIcon>
			</SocialContainer>
		</Left>
		<Center>
			<Title>Useful Links</Title>
			<List>
				<ListItem>Home</ListItem>
				<ListItem>Cart</ListItem>
				<ListItem>De-Icers</ListItem>
				<ListItem>Screen Kits</ListItem>
				<ListItem>Thrusters</ListItem>
				<ListItem>Parts</ListItem>
			</List>
		</Center>
		<Right>
			<Title>Contact</Title>
			<ContactItem><Room style={{marginRight:"10px"}} />11 Charles Street, Suite 1001</ContactItem>
			<ContactItem><Phone style={{marginRight:"10px"}} />647-547-5467</ContactItem>
			<ContactItem><MailOutline style={{marginRight:"10px"}} />info@weedsbgone.com</ContactItem>
			<Payment src='https://i.ibb.co/Qfvn4z6/payment.png' />
		</Right>
	</Container>
  )
}

export default Footer